import {AfterViewInit, Component, OnInit, ViewChild} from '@angular/core';
import {MatDialog } from '@angular/material/dialog';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api-service/api-service.service';
import {MatPaginator} from '@angular/material/paginator';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
export interface UserModel {
  brgy: string;
  dob: string;
  farm_address: string;
  first_name: string;
  gender: string;
  house: string;
  id: number;
  last_name: string;
  lot_no: string;
  middle_name: string;
  municipality: string;
  province: string;
  ref_number: string;
  region: string;
  street: string;
  ten_status: string;
  total_farm_area: string;
  user_contact_number: string;
  user_id: number;
}
export interface Common {
  id: number;
  name: string;
}
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  clickedRows = new Set<UserModel>();
  filterFormGroup: FormGroup;
  panelOpenState = false;
  filterIsOpen = false;
  // dataSources = new MatTableDataSource<UserModel>(PROFILE_DATA);
  userData: any = [];
  municipalityData: any = {"municipalities": [{"id":0 ,"name":"All"}]};
  data: any = [];
  dataSource = new MatTableDataSource<UserModel>();
  errorMessage = '';

  municipalityControl = new FormControl();
  municipalityOptions: Common[];
  municipalityFilteredOptions: Observable<Common[]>;

  displayedColumns: string[] = [
    'refNo', 'lastName', 'firstName', 'municipality',
    'province', 'lotNo', 'farmAddress', 'tenStatus', 'noHas'
  ];

  constructor(
    public dialog: MatDialog,
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private router: Router) { }
  ngOnInit(): void {
    this.initFilterFormGroup();

    this.apiService
    .getAllFarmers({page: 1})
    .subscribe(res => {

      this.data.push(res);
      console.log(res);
      this.userData.push(this.data[0].profiles);
      const profileData: UserModel[] = this.userData[0];
      this.dataSource = new MatTableDataSource(profileData);
      this.dataSource.sort = this.sort;
      setTimeout(() => this.dataSource.paginator = this.paginator);

      this.municipalityControl.setValue({"id":0 ,"name":"All"});
      this.data[0].municipalities.forEach((element: any) => {
        this.municipalityData.municipalities.push(element);
      });
      
      this.municipalityOptions = this.municipalityData.municipalities;
      this.filterMunicipality();
    }, err => {
      this.errorMessage = err.error;
    });
  }

  filterMunicipality(): void{
    this.municipalityFilteredOptions = this.municipalityControl.valueChanges
    .pipe(
      startWith(''),
      map(value => typeof value === 'string' ? value : value.name),
      map(name => name ? this._filterMunicipality(name) : this.municipalityOptions.slice())
    );
  }

  displayFn(common: Common): string {
    return common && common.name ? common.name : '';
  }

  private _filterMunicipality(name: string): Common[] {
    const filterValue = name.toLowerCase();
    return this.municipalityOptions.filter(option => option.name.toLowerCase().indexOf(filterValue) === 0);
  }

  initFilterFormGroup(): void{
    this.filterFormGroup = this.formBuilder.group({
      irrigated: [true],
      upland: [true],
      lowland: [true]
    });
  }

  addFarmer(): void {
    localStorage.setItem('profileID', '');
    this.router.navigate(['farmer']);
  }
  backToDashboard(): void{
    this.router.navigate(['home']);
  }
  openDialog(): void {
    this.filterIsOpen = true;
    // this.dialog.open(FilterDialogComponent);
  }
  closeDialog(): void {
    this.filterIsOpen = false;
    // this.dialog.open(FilterDialogComponent);
  }
  selectFarmer(row: any):void {
    localStorage.setItem('profileID', row.id);
    this.router.navigate(['farmer']);
  }

}
export interface UserModel {
  brgy: string;
  dob: string;
  farm_address: string;
  first_name: string;
  gender: string;
  house: string;
  id: number;
  last_name: string;
  lot_no: string;
  middle_name: string;
  municipality: string;
  province: string;
  ref_number: string;
  region: string;
  street: string;
  ten_status: string;
  total_farm_area: string;
  user_contact_number: string;
  user_id: number;
}

